.btn-sidebar{
	float: left;
	@nest &.enabled &{
		@nest &__line{
			@nest &:first-child{
				left: 25px;
				transform: rotate(28deg);
				-webkit-animation: arrow-top-enabled 200ms ease;
			}
			@nest &:last-child{
				left: 25px;
				transform: rotate(-28deg);
				-webkit-animation: arrow-bot-enabled 200ms ease;
			}
		}
	}
	@nest &__line{
		height: 5px;
		display: block;
		background: #fff;
		position: relative;
		transition: left 200ms, transform 200ms;
		@nest &:first-child{
			position: absolute;
			content: '';
			left: 16px;
			top: 15px;
			width: 22px;
			transform: rotate(-28deg);
		}
		@nest &:nth-child(2){
			top: -1px;
			width: 33px;
			margin: auto;
			position: relative;
		}
		@nest &:last-child{
			position: absolute;
			content: '';
			left: 16px;
			bottom: 17px;
			width: 22px;
			transform: rotate(28deg);
		}
	}
}

@keyframes arrow-top-enabled{
	0%{
		left: 16px;
		transform: rotate(-28deg);
	}
	10%{
		left: 16px;
		transform: rotate(-21deg);
	}
	20%{
		left: 17px;
		transform: rotate(-14deg);
	}
	30%{
		left: 18px;
		transform: rotate(-7deg);
	}
	40%{
		left: 19px;
		transform: rotate(0deg);
	}
	50%{
		left: 20px;
		transform: rotate(7deg);
	}
	60%{
		left: 21px;
		transform: rotate(14deg);
	}
	70%{
		left: 22px;
		transform: rotate(14deg);
	}
	80%{
		left: 23px;
		transform: rotate(21deg);
	}
	90%{
		left: 24px;
		transform: rotate(28deg);
	}
	100%{
		left: 25px;
		transform: rotate(28deg);
	}
}

@keyframes arrow-bot-enabled{
	0%{
		left: 16px;
		transform: rotate(28deg);
	}
	10%{
		left: 16px;
		transform: rotate(21deg);
	}
	20%{
		left: 17px;
		transform: rotate(14deg);
	}
	30%{
		left: 18px;
		transform: rotate(7deg);
	}
	40%{
		left: 19px;
		transform: rotate(0deg);
	}
	50%{
		left: 20px;
		transform: rotate(-7deg);
	}
	60%{
		left: 21px;
		transform: rotate(-14deg);
	}
	70%{
		left: 22px;
		transform: rotate(-14deg);
	}
	80%{
		left: 23px;
		transform: rotate(-21deg);
	}
	90%{
		left: 24px;
		transform: rotate(-28deg);
	}
	100%{
		left: 25px;
		transform: rotate(-28deg);
	}
}