.sidebar-contacts{
	margin-bottom: 26px;
	@media (max-width: 1499px){
		margin-bottom: 8px;
	}
	@nest & span{
		font-size: 18px;
		color: #fff;
		display: block;
		margin-bottom: 7px;
		@media (max-width: 1499px){
			font-size: 14px;
			margin-bottom: 4px;
		}
		@media (max-width: 1199px){
			font-size: 14px;
		}
	}
	@nest & a{
		font-size: 36px;
		color: #fff;
		display: block;
		line-height: normal;
		font-family: var(--fontP);
		@media (max-width: 1499px){
			font-size: 30px;
		}
		@media (max-width: 1199px){
			font-size: 27px;
		}
	}
}