.footer{
	background: #41a28c;
	padding: 19px 0 3px;
	@media (max-width: 1499px){
		padding-bottom: 17px;
	}
	@media (max-width: 991px){
		padding: 13px 0 23px;
	}
	@media (max-width: 767px){
		text-align: center;
	}
	@media (max-width: 575px){
		padding: 15px 0 32px;
	}
	@nest & span{
		font-size: 18px;
		color: #fff;
		display: block;
		line-height: normal;
		margin-bottom: 17px;
		@media (max-width: 1499px){
			font-size: 14px;
			line-height: 19px;
			margin: 2px 0 14px;
		}
		@media (max-width: 575px){
			line-height: 19px;
			margin: 0 0 14px;
		}
	}
	@nest &__right{
		float: right;	
		text-align: right;
		@media (max-width: 767px){
			float: none;
			text-align: center;
		}
		@nest & span{
			margin-bottom: 0;
			margin-right: 25px;
			display: inline-block;
			vertical-align: middle;
			@media (max-width: 1499px){
				line-height: 17px;
				margin: 9px 20px 0 0;
			}
			@media (max-width: 991px){
				margin-right: 20px;
			}
			@media (max-width: 767px){
				margin: 0;
			}
		}
		@nest & img{
			margin: 4px 5px 0 0;
			display: inline-block;
			vertical-align: middle;
			@media (max-width: 1499px){
				max-width: 105px;
				margin: 9px 5px 0 0;
			}
			@media (max-width: 991px){
				margin-right: 1px;
			}
			@media (max-width: 767px){
				display: none;
			}
		}
	}
}