.portfolio-block{
	margin-bottom: 20px;
	text-align: left;
	border: 3px solid #0e4438;
	@media (max-width: 1199px){
		margin-bottom: 25px;
	}
	@media (max-width: 575px){
		max-width: 137px;
		margin: 0 auto 16px;
	}
	@nest & img{
		@media (max-width: 1499px){
			height: 223px;
		}
		@media (max-width: 1199px){
			height: 153px;
		}
		@media (max-width: 991px){
			height: 159px;
		}
		@media (max-width: 575px){
			height: 98px;
		}
	}
	@nest &__text{
		padding: 10px 15px 27px 19px;
		@media (max-width: 1499px){
			padding: 13px 10px 10px 11px;
		}
		@media (max-width: 1199px){
			padding: 8px 10px 6px 8px;
		}
		@media (max-width: 991px){
			padding: 9px 10px 6px 8px;
		}
		@media (max-width: 575px){
			padding: 8px 5px 7px 12px;
		}
		@nest & span{
			font-size: 24px;
			color: #0e4438;
			font-weight: 700;
			display: block;
			margin-bottom: 11px;
			line-height: normal;
			@media (max-width: 1499px){
				font-size: 14px;
				margin-bottom: 18px;
			}
			@media (max-width: 1199px){
				margin-bottom: 10px;
			}
			@media (max-width: 575px){
				font-size: 17px;
				line-height: 21px;
				margin-bottom: 0;
			}
		}
		@nest & p{
			margin: 0;
			font-size: 20px;
			color: #0e4438;
			line-height: 24px;
			@media (max-width: 1499px){
				height: 56px;
				font-size: 14px;
				line-height: 18px;
				overflow: hidden;
			}
			@media (max-width: 1199px){
				height: 85px;
				line-height: 17px;
			}
			@media (max-width: 991px){
				height: 90px;
				line-height: 18px;
			}
			@media (max-width: 575px){
				display: none;
			}
		}
	}
}