.btn-menu{
	float: right;
	@nest &__line{
		position: absolute;
		content: '';
		top: 10px;
		left: 15px;
		width: 33px;
		height: 5px;
		display: block;
		background: #fff;
		transition: top 300ms, transform 200ms;
		@nest &:nth-child(2){
			top: 21px;
		}
		@nest &:last-child{
			top: 32px;
		}
	}
	@nest &.open &{
		@nest &__line{
			@nest &:first-child{
				top: 50%;
				transform: translateY(-50%) rotate(45deg);
			}
			@nest &:nth-child(2){
				opacity: 0;
			}
			@nest &:nth-child(3){
				top: 50%;
				transform: translateY(-50%) rotate(-45deg);
			}
		}
	}
}