.nav-list{
	padding: 0;
	width: 100%;
	margin: 0;
	text-align: right;
	padding-right: 72px;
	list-style: none;
	@media (max-width: 1499px){
		padding-right: 35px;
	}
	@media (max-width: 1199px){
		padding-right: 25px;
	}
	@media (max-width: 991px){
		background: #292929;
		padding: 36px 10px 30px 39px;
	}
	@nest &__item{
		margin-left: 83px;
		display: inline-block;
		@media (max-width: 1499px){
			margin-left: 40px;
		}
		@media (max-width: 991px){
			display: block;
			margin-left: 0;
			margin-bottom: 20px;
		}
		@nest &:last-child{
			margin-bottom: 0;
		}
	}
	@nest &__link{
		font-size: 30px;
		color: #fff;
		font-weight: 800;
		text-decoration: none;
		position: relative;
		@media (max-width: 1499px){
			font-size: 24px;
		}
		@media (max-width: 1199px){
			font-size: 18px;
		}
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			right: 0;
			bottom: 0;
			width: 0;
			height: 2px;
			border-radius: 1px;
			background: #fff;
			transition: width 300ms;
		}
		@nest &:hover{
			@nest &:after{
				width: 100%;
			}
		}
	}
}
