.sidebar-form{
	max-width: 360px;
	margin: 102px 0 0 54px;
	@media (max-width: 1499px){
		margin-top: 115px;
		margin-left: 0;
	}
	@media (max-width: 1199px){
		margin-top: 41px;
	}
	@nest & span{
		font-size: 48px;
		color: #fff;
		display: block;
		margin-bottom: 10px;
		font-family: var(--fontP);
		@media (max-width: 1499px){
			font-size: 36px;
			margin-bottom: 13px;
		}
		@media (max-width: 1199px){
			font-size: 30px;
			margin-bottom: 7px;
		}
	}
	@nest & input{
		font-size: 36px;
		color: #292929;
		height: 66px;
		width: 100%;
		outline: none;
		padding-left: 15px;
		background: #fff;
		margin-bottom: 22px;
		font-family: var(--fontP);
		border: 3px solid #292929;
		@media (max-width: 1499px){
			font-size: 24px;
			margin-bottom: 15px;
		}
		@media (max-width: 1199px){
			height: 41px;
			font-size: 18px;
		}
		@nest &::placeholder{
			font-size: 36px;
			color: #292929;
			font-family: var(--fontP);
			@media (max-width: 1499px){
				font-size: 24px;
			}
			@media (max-width: 1199px){
				font-size: 18px;
			}
		}
	}
	@nest & textarea{
		font-size: 36px;
		color: #292929;
		resize: none;
		width: 100%;
		outline: none;
		padding-left: 15px;
		min-height: 230px;
		margin-bottom: 20px;
		font-family: var(--fontP);
		border: 3px solid #292929;
		@media (max-width: 1499px){
			font-size: 24px;
		}
		@media (max-width: 1199px){
			font-size: 18px;
			min-height: 225px;
			margin-bottom: 7px;
		}
		@nest &::placeholder{
			font-size: 36px;
			color: #292929;
			font-family: var(--fontP);
			@media (max-width: 1499px){
				font-size: 24px;
			}
			@media (max-width: 1199px){
				font-size: 18px;
			}
		}
	}
}