.title{
	padding: 0 15px;
	position: relative;
	text-align: center;
	@media (max-width: 575px){
		padding: 0;
	}
	@nest &:after{
		position: absolute;
		content: '';
		top: 39px;
		height: 3px;
		left: 0;
		right: 0;
		background: #41a28c;
		@media (max-width: 1499px){
			top: 23px;
			height: 2px;
		}
		@media (max-width: 1199px){
			top: 19px;
		}
		@media (max-width: 575px){
			display: none;
		}
	}
	@nest & span{
		z-index: 10;
		font-size: 60px;
		color: #0e4438;
		padding: 0 20px;
		background: #fff;
		position: relative;
		text-align: center;
		line-height: normal;
		display: inline-block;
		font-family: var(--fontP);
		@media (max-width: 1499px){
			font-size: 36px;
			padding: 0 15px;
			letter-spacing: -.6px;
		}
		@media (max-width: 1199px){
			font-size: 30px;
		}
		@media (max-width: 575px){
			padding: 0;
		}
	}
	@nest &--lg{
		@media (max-width: 991px){
			margin: 42px 0 14px;
		}
		@media (max-width: 575px){
			margin-bottom: 29px;
		}
		@nest &:after{
			top: 45px;
			@media (max-width: 1499px){
				top: 29px;
				height: 3px;
			}
			@media (max-width: 1199px){
				top: 24px;
			}
			@media (max-width: 991px){
				top: 26px;
			}
		}
		@nest & span{
			font-size: 72px;
			font-weight: 400;
			@media (max-width: 1499px){
				font-size: 48px;
			}
			@media (max-width: 1199px){
				font-size: 37px;
			}
			@media (max-width: 991px){
				font-size: 43px;
			}
			@media (max-width: 575px){
				font-size: 36px;
				letter-spacing: -1px;
			}
		}
	}
}