.billboard{
	padding: 152px 20px 168px 236px;
	background: resolve('billboard.jpg') no-repeat center;
	-webkit-background-size: cover;
	background-size: cover;
	@media (max-width: 1499px){
		padding: 101px 20px 180px 94px;
	}
	@media (max-width: 1199px){
		padding: 45px 20px 72px 67px;
	}
	@media (max-width: 991px){
		padding: 50px 20px 67px 126px;
	}
	@media (max-width: 767px){
		text-align: center;
		padding: 13px 15px 24px;
	}
	@nest & span{
		font-size: 100px;
		color: #fff;
		display: block;
		font-weight: 700;
		max-width: 700px;
		padding-left: 56px;
		line-height: 120px;
		position: relative;
		margin-bottom: 120px;
		font-family: var(--fontP);
		@media (max-width: 1499px){
			font-size: 72px;
			padding-left: 38px;
			line-height: 87px;
			margin-bottom: 64px;
		}
		@media (max-width: 1199px){
			font-size: 60px;
			line-height: 72px;
			margin-bottom: 48px;
		}
		@media (max-width: 767px){
			font-size: 36px;
			line-height: 43px;
			padding-left: 0;
			margin-bottom: 25px;
		}
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			top: 22px;
			width: 11px;
			height: 324px;
			background: #fff;
			@media (max-width: 1499px){
				width: 10px;
				top: 19px;
				height: 223px;
			}
			@media (max-width: 1199px){
				top: 12px;
				height: 187px;
			}
			@media (max-width: 767px){
				display: none;
			}
		}
		@nest & br{
			@media (max-width: 767px){
				display: none;
			}
		}
	}
	@nest & p{
		font-size: 24px;
		color: #fff;
		font-weight: 700;
		max-width: 945px;
		line-height: 30px;
		margin-bottom: 26px;
		@media (max-width: 1499px){
			font-size: 18px;
			line-height: 22px;
			margin-bottom: 20px;
		}
		@media (max-width: 1199px){
			font-size: 14px;
			line-height: 17px;
			margin-bottom: 16px;
		}
		@media (max-width: 767px){
			font-weight: 400;
		}
		@nest &:last-child{
			display: none;
		}
	}
}