.history{
	margin-bottom: 60px;
	@media (max-width: 1499px){
		margin-bottom: 55px;
	}
	@media (max-width: 991px){
		margin-bottom: 28px;
	}
	@media (max-width: 767px){
		margin-bottom: 20px;
	}
	@nest & .title{
		margin-bottom: 30px;
		@media (max-width: 1499px){
			margin-bottom: 19px;
		}
		@media (max-width: 1199px){
			margin-bottom: 11px;
		}
		@media (max-width: 991px){
			margin-bottom: 14px;
		}
	}
	@nest & p{
		font-size: 20px;
		color: #0e4438;
		margin-bottom: 25px;
		@media (max-width: 1499px){
			font-size: 14px;
			margin-bottom: 12px;
			line-height: normal;
		}
		@media (max-width: 1199px){
			margin-bottom: 16px;
			line-height: 17px;
		}
	}
}