.header{
	@media (max-width: 991px){
		position: relative;
	}
	@nest &__mobile{
		@media (max-width: 991px){
			background: #292929;
			text-align: center;
		}
	}
	@nest &__logo{
		font-size: 48px;
		color: #fff;
		display: block;
		font-weight: 800;
		background: #052c23;
		text-align: center;
		line-height: 81px;
		min-height: 81px;
		padding-right: 15px;
		text-transform: uppercase;
		@nest &:hover, &:focus{
			color: #fff;
			text-decoration: none;
		}
		@media (max-width: 1499px){
			font-size: 36px;
			padding-right: 0;
			line-height: 73px;
		}
		@media (max-width: 1199px){
			font-size: 30px;
			min-height: 64px;
			line-height: 64px;
		}
		@media (max-width: 991px){
			min-height: 49px;
			line-height: 49px;
			display: inline-block;
			background: transparent;
		}
		@media (max-width: 767px){
			font-size: 24px;
		}
	}
}