.sidebar{
	height: 100%;
	@media (max-width: 991px){
		position: absolute;
		content: '';
		left: 0;
		top: 0;
		bottom: 0;
		max-width: 241px;
		background: #0e4438;
		transition: transform 300ms;
		transform: translateX(-200%);
	}
	@nest &.open{
		z-index: 100;
		transform: translateX(0);
	}
	@nest &__contacts{
		padding-left: 69px;
		@media (max-width: 1499px){
			padding-left: 41px;
		}
		@media (max-width: 1199px){
			padding-left: 15px;
		}
	}
}