.why-block{
	max-width: 335px;
	text-align: center;
	margin: 20px auto 31px;
	@media (max-width: 1499px){
		margin: 15px auto 17px;
	}
	@media (max-width: 1199px){
		margin-bottom: 13px;
	}
	@media (max-width: 575px){
		margin: 21px auto 4px;
	}
	@nest &__logo{
		width: 150px;
		height: 150px;
		background: #cfdad7;
		border-radius: 50%;
		margin: 0 auto 13px;
		@media (max-width: 1499px){
			width: 100px;
			height: 100px;
			margin-bottom: 12px;
		}
		@media (max-width: 1199px){
			width: 80px;
			height: 85px;
			margin-bottom: 7px;
		}
		@media (max-width: 991px){
			margin-bottom: 12px;
		}
		@media (max-width: 575px){
			margin-bottom: 18px;
		}
	}
	@nest & span{
		font-size: 30px;
		color: #000;
		display: block;
		line-height: 38px;
		@media (max-width: 1499px){
			margin: auto;
			font-size: 20px;
			max-width: 215px;
			line-height: 24px;
			letter-spacing: -.5px;
		}
		@media (max-width: 1199px){
			font-size: 17px;
			line-height: 21px;
		}
		@media (max-width: 575px){
			font-size: 11px;
			line-height: 13px;
		}
	}
}