@import "bootstrap/dist/css/bootstrap-reboot.min.css";
@import "normalize-css/normalize.css";
/*! main */
@font-face {
  font-family: 'pf_din_text_comp';
  src: url('../fonts/pfdintextcomppro-regular_1.woff2') format('woff2'),
  url('../fonts/pfdintextcomppro-regular_1.woff') format('woff'),
  url('../fonts/pfdintextcomppro-regular_1.ttf') format('truetype'),
  url('../fonts/pfdintextcomppro-regular_1.svg#pf_din_text_comp_proregular') format('svg');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'pf_din_text_comp';
  src: url('../fonts/pfdintextcomppro-bold_1.woff2') format('woff2'),
  url('../fonts/pfdintextcomppro-bold_1.woff') format('woff'),
  url('../fonts/pfdintextcomppro-bold_1.ttf') format('truetype'),
  url('../fonts/pfdintextcomppro-bold_1.svg#pf_din_text_comp_probold') format('svg');
