  font-style: normal;
}
@font-face {
  font-family: 'open_sans';
  src: url('../fonts/2211_1.woff2') format('woff2'),
  url('../fonts/2211_1.woff') format('woff'),
  url('../fonts/2211_1.ttf') format('truetype'),
  url('../fonts/2211_1.svg#open_sansregular') format('svg');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'open_sans';
  src: url('../fonts/2204.woff2') format('woff2'),
  url('../fonts/2204.woff') format('woff'),
  url('../fonts/2204.ttf') format('truetype'),
  url('../fonts/2204.svg#open_sansbold') format('svg');
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: 'open_sans';
  src: url('../fonts/2206.woff2') format('woff2'),
  url('../fonts/2206.woff') format('woff'),
  url('../fonts/2206.ttf') format('truetype'),
  url('../fonts/2206.svg#open_sansextrabold') format('svg');
  font-weight: 800;
  font-style: normal;
}

html, body{
	font-family: 'open_sans';
	font-weight: 400;
}

:root{
	--fontP: 'pf_din_text_comp';
}

.main-row{
  width: 100%;
  display: table;
  @media (max-width: 991px){
    display: block;
    position: relative;
  }
}
.main-col{
  @nest &__left{
    width: 25%;
    background: #0e4438;
    display: table-cell;
    @media (max-width: 991px){
      width: 100%;
      display: block;
    }
  }
  @nest &__right{
    width: 75%;
    display: table-cell;
    vertical-align: middle;
    @media (max-width: 991px){
      width: 100%;
      display: block;
    }
    @nest &--bg{
      background: #292929;
    }
  }
}
.row{
  @nest &--sm&{
    margin-left: -7.5px;
    margin-right: -7.5px;
    @media (max-width: 1199px){
      margin-left: -15px;
      margin-right: -15px;
    }
    @media (max-width: 575px){
      margin: 0 -10px;
    }
  }
  @nest &--sm &{
    @nest &__col-sm{
      padding: 0 7.5px;
      @media (max-width: 1199px){
        padding: 0 15px;
      }
      @media (max-width: 575px){
        width: 50%;
        padding: 0 10px;
      }
      @nest &:last-child{
        @media (min-width: 768px){
          display: none;
        }
      }
    }
  }
}
.row__col-xs{
  @media (max-width: 575px){
    width: 50% !important;
  }
}