.portfolio{
	margin: 62px 0;
	@media (max-width: 1499px){
		margin: 76px 0 45px;
	}
	@media (max-width: 1199px){
		margin: 27px 0;
	}
	@media (max-width: 991px){
		margin: 43px 0 0;
	}
	@media (max-width: 575px){
		margin-top: 22px;
	}
	@nest & .container-fluid{
		@media (max-width: 575px){
			padding-left: 10px;
			padding-right: 10px;
		}
	}
	@nest &__inner{
		margin: 23px 0 0;
		text-align: center;
		@media (max-width: 1499px){
			margin-top: 41px;
		}
		@media (max-width: 1199px){
			margin-top: 27px;
		}
		@media (max-width: 991px){
			margin-top: 30px;
		}
	}
	@nest &__more{
		font-size: 36px;
		color: #292929;
		padding: 0;
		height: 59px;
		outline: none;
		padding: 0 40px;
		cursor: pointer;
		background: #41a28c;
		font-family: var(--fontP);
		border: 3px solid #292929;
		transition: background 300ms;
		@nest &:hover{
			background: #16d9ad;
		}
	}
	@nest & .btn-form{
		@media (max-width: 1499px){
			font-size: 36px;
		}
		@media (max-width: 1199px){
			font-size: 24px;
			display: inline-block;
		}
		@media (max-width: 991px){
			min-height: 48px;
			line-height: 42px;
			padding: 0 45px;
		}
	}
}