.btn-form{
	font-size: 36px;
	color: #292929;
	padding: 0;
	outline: none;
	cursor: pointer;
	padding: 0 40px;
	min-height: 59px;
	line-height: 53px;
	background: #41a28c;
	text-align: center;
	display: inline-block;
	font-family: var(--fontP);
	border: 3px solid #292929;
	transition: background 300ms;
	@nest &:hover{
		background: #16d9ad;
	}
	@media (max-width: 1499px){
		font-size: 30px;
	}
	@media (max-width: 1199px){
		font-size: 24px;
		min-height: 44px;
		line-height: 38px;
		display: block;
		margin: auto;
	}
}