.sidebar-nav{
	margin: 0 0 44px;
	@media (max-width: 1499px){
		margin-bottom: 20px;
	}
	@media (max-width: 1199px){
		margin-bottom: 30px;
	}
	@nest &__list{
		margin: 0;
		padding: 26px 0 0;
		list-style: none;
		@media (max-width: 1499px){
			padding-top: 15px;
		}
		@media (max-width: 1199px){
			padding-top: 11px;
		}
	}
	@nest &__link{
		font-size: 24px;
		color: #fff;
		font-weight: 700;
		display: block;
		position: relative;
		margin: 0 0 1.5px;
		padding: 11px 0 9px 164px;
		transition: background 300ms;
		@nest &:hover{
			background: #41a28c;
		}
		@media (max-width: 1499px){
			font-size: 20px;
			margin-bottom: 0;
			line-height: 48px;
			padding: 0 0 0 110px;
		}
		@media (max-width: 1199px){
			font-size: 18px;
			padding-left: 77px;
			letter-spacing: -.8px;
			line-height: 43px;
		}
		@nest & img{
			position: absolute;
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			@media (max-width: 1499px){
				transform: translateY(-50%) scale(.8);
			}
			@media (max-width: 1199px){
				transform: translateY(-50%) scale(.7);
			}
		}
	}
}